.player-match-hero-icon {
    height: 39px;
    width: 70px;
    margin-left: 5px
}

.win {
    color: #00bc8c !important;
}

.lose {
    color: #bf2718 !important;
}

.progress-small {
    height: 10px !important;
}

.shrink {
    display: inline-block;
}

.circle-border {
    border-radius: 50%;
    text-align: center;
    text-transform: none;
    cursor: help;
    border: 1px solid #999999;
}